import React from 'react';
import { RegionItem, SearchItem } from '../../modules/detail';
import Link from 'next/link';
import { CardImage, CardWrapper, CardMeta } from './style';
import { Image } from 'antd';

interface ITourItem {
  list: SearchItem | RegionItem;
}

const TourItem: React.FC<ITourItem> = ({ list }) => {
  const { title, addr1, firstimage, contentid, contenttypeid } = list;

  return (
    <Link href={`/detail/${contenttypeid}/${contentid}`} passHref>
      <a>
        <CardWrapper
          hoverable
          cover={
            firstimage ? (
              <CardImage
                alt={title}
                src={firstimage}
                width={280}
                height={220}
                placeholder="empty"
              />
            ) : (
              <Image
                alt="준비중"
                src="error"
                fallback="data:image/png;base64,"
                width={300}
                height={220}
              />
            )
          }
        >
          <CardMeta title={title} description={addr1} />
        </CardWrapper>
      </a>
    </Link>
  );
};

export default TourItem;
